.counter {
    width: 80%;
    height: 50%;
    border: 3px solid #466b9f;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.valueSpan {
    font-size: 50px;
    color: #bdbdbd;
}

.maxValue {
    font-size: 80px;
    color: red;
}